import { createRouter, createWebHashHistory } from 'vue-router'
import HomePage from "../components/home.vue"
import EndPage from "../components/end.vue"
import BasicPage from "../components/basic.vue"
import PapicPage from "../components/papic.vue"
import PersonPage from "../components/person.vue"
const routes = [
  {
    path: '/',
    component: HomePage,
  },
  {
    path:'/end',
    component:EndPage
  },
  {
    path:'/basic',
    component:BasicPage
  },
  {
    path:'/person',
    component:PersonPage
  },
  {
    path:'/papic',
    component:PapicPage
  },
]

export default createRouter({
history: createWebHashHistory(),
routes,
})

