<template>
  <div class="back">
    <el-container>
        <el-header>
            <el-row type="flex" justify="start" align="middle">
                <el-col :span="6" :offset="3">
                    <el-image :src="require('@/assets/p2/p2-tan.png')"></el-image>
                </el-col>
                <el-col :span="6">
                    基本信息
                </el-col>
            </el-row>
        </el-header>
        <el-main>
            <el-row type="flex" :gutter="20" align="middle" >
                    <el-col :xs="18" :sm="16" :md="12" :lg="12">
                        <el-form :model="form"  :rules="rules" ref="form">
                            <el-form-item  prop="name">
                                <el-input v-model="form.name" placeholder="姓名"></el-input>
                            </el-form-item>
                            <el-form-item prop="sex" >
                                 <el-select v-model="form.sex"  placeholder="性别">
                                    <el-option label="男" value="man"></el-option>
                                    <el-option label="女" value="woman"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item  prop="origin" >
                                <el-input v-model="form.origin" placeholder="籍贯(留学生请写国籍)"></el-input>
                            </el-form-item>
                            <el-form-item prop="birthday">
                                 <el-date-picker 
                                 type="date" 
                                 placeholder="生日" 
                                 style="width:100%;height: 30px;font-size: 16px;"
                                 v-model="form.birthday"
                                 >
                                 </el-date-picker>
                            </el-form-item>
                            <el-form-item prop="major">
                                <el-input v-model="form.major" placeholder="就读专业/大类"></el-input>
                            </el-form-item>
                            <el-form-item prop="class">
                                <el-input v-model="form.class" placeholder="行政班级"></el-input>
                            </el-form-item>
                            <el-form-item prop="dorm">
                                <el-input v-model="form.dorm" placeholder="寝室"></el-input>
                            </el-form-item>
                            <el-form-item prop="number">
                                <el-input v-model="form.number" placeholder="学号"></el-input>
                            </el-form-item>
                            <el-form-item prop="qq">
                                <el-input v-model="form.qq" placeholder="QQ"></el-input>
                            </el-form-item>
                            <el-form-item prop="mail">
                                <el-input v-model="form.mail" placeholder="邮箱"></el-input>
                            </el-form-item>
                            <el-form-item prop="phone">
                                <el-input v-model="form.phone" placeholder="手机号码"></el-input>
                            </el-form-item>
                            <el-form-item prop="photo">
                                <el-upload
                                action="http://localhost:7001/upload"
                                accept="image/*"
                                :data="{ number:form.number }"
                                :auto-upload="true"
                                :on-success="uploadsuccess"
                                :on-error="uploaderror"
                                :before-upload="BeforeUpload"
                                :show-file-list="false"
                                style="width:100%;height:30px"
                                >
                                 <el-button class="uploadbutton" style="height:30px">上传一寸证件照(2MB)</el-button>
                                </el-upload>
                            </el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :span="2">
                        <div @click="UpdateBasic">
                            继续
                        </div>
                    </el-col>
                    <el-col :span="3" @click="UpdateBasic">
                        <el-image :src="require('@/assets/p2/p2-tanright.png')" :fit="cover"/>
                    </el-col>
                </el-row>
        </el-main>
    </el-container>
  </div>
</template>

<script>
import { Form } from "../store/index"
export default {
  name: 'BasicPage',
  setup(){
    const BasicStore=Form()
    return {BasicStore}
  },
  data(){
    return{
        rules: 
        {
          name: [{
                required: true,
                message: '请别忘了填写你的姓名',
                trigger: 'blur',
                }],
          sex: [{
                required: true,
                message: '请别忘了填写你的性别',
                trigger: 'blur',
                }],
          origin: [{
                required: true,
                message: '请别忘了填写你的籍贯',
                trigger: 'blur',
                }], 
          birthday: [{
                required: true,
                message: '请别忘了填写你的生日',
                trigger: 'blur',
                }],
          major: [{
                required: true,
                message: '请别忘了填写你的专业',
                trigger: 'blur',
                }],
          dorm: [{
                required: true,
                message: '请别忘了填写你的寝室',
                trigger: 'blur',
                }],
          class: [{
                required: true,
                message: '请别忘了填写你的班级',
                trigger: 'blur',
                }],
          number: [{
                required: true,
                message: '请别忘了填写你的学号',
                trigger: 'blur',
                }],
          qq: [{
                required: true,
                message: '请别忘了填写你的QQ',
                trigger: 'blur',
                }],
          mail: [{
                required: true,
                message: '请别忘了填写你的邮箱',
                trigger: 'blur',
                }],
          phone: [{
                required: true,
                message: '请别忘了填写你的手机号码',
                trigger: 'blur',
                }],
        }
  }
 },
 computed:{
    form:{
        get(){
            return this.BasicStore.$state.basic
        }
    } 
 },
 methods:{
    UpdateBasic(){
         this.BasicStore.updatebasic(this.form)
         this.$router.push('/person')
    }
 }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.back{
  top: 0;
  left: 0;
  position: fixed;
  overflow: scroll;
  background-color: black;
  background-image: url(../assets/p2/p2-2.png),url(../assets/p2/p2-1.jpeg);
  width: 100%;
  height: 100%;
  background-size: 100% 100%,100% auto;
  background-repeat: repeat,no-repeat;
}
/* :deep(.el-textarea__inner,.el-input__inner,){
    background-color: black ;
}
:deep(.el-form-item){
    background-color: unset ;
} */
:root{
    --el-fill-color-blank:unset;
    --el-text-color-regular:gray;
}
</style>
