<template>
  <div class="back">
     <el-container style="margin-top:100px">
        <el-header style="display:flex;flex-direction:column;justify-content: center;" >
            <el-row justify="center" :gutter="20" align="middle">
                <el-col :span="3" @click="pre">
                    <el-image :src="require('@/assets/p4/p4-tanleft.png')" :fit="cover" />
                </el-col>
                    <el-col :span="6" >
                    继续？
                    </el-col>
                    <el-col :span="3" @click="submit" >
                        <el-image :src="require('@/assets/p4/p4-tanright.png')" :fit="cover"/>
                    </el-col>
            </el-row>
        </el-header>
        <el-main>
            <el-row type="flex" justify="center">
                    <el-col :xs="21" :sm="20" :md="12" :lg="12">
                        <el-form :model="form"  :rules="rules" ref="form">
                            <el-form-item  prop="department">
                                <el-row>选择自己想加入的部门,并选择是否接收调剂</el-row>
                                <el-row>(最多选择两个,选择两个时需进行排序)</el-row>
                                <el-row :gutter="15">
                                  <el-col :span="10">
                                      是否调剂
                                  </el-col>
                                  <el-col :span="6">
                                    <el-radio label="是" v-model="form.adjustment">是</el-radio>
                                  </el-col>
                                  <el-col :span="6">
                                    <el-radio label="否" v-model="form.adjustment">否</el-radio>
                                  </el-col>
                                </el-row>
                                <el-cascader v-model="form.department1" :options="options" placeholder="请选择"></el-cascader>
                                <el-cascader v-model="form.department2" :options="options" placeholder="请选择" ></el-cascader>
                            </el-form-item>
                            <el-form-item prop="reason" >
                              <el-row>
                                理由
                              </el-row>
                              <el-input type="textarea" :rows="3" placeholder="" v-model="form.reason"></el-input>
                            </el-form-item>
                            <el-form-item  prop="want" >
                              <el-row>
                                对勤创有没有什么想说的(选填,100字以内)
                              </el-row>
                              <el-input type="textarea" :rows="5" placeholder="" v-model="form.want"></el-input>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-row>
        </el-main>
        <el-footer>
          <el-row :gutter="20" justify="center" align="middle">
            <el-col :span="6">
              <el-image :src="require('@/assets/p4/p4-tan.png')" :fit="cover" />
            </el-col>
            <el-col :span="6">
              我与勤创
            </el-col>
          </el-row>
        </el-footer>
    </el-container>
  </div>
</template>

<script>
import { Form } from "../store/index"
export default {
  name: 'PapicPage',
  setup(){
    const PapicStore=Form()
    return {PapicStore}
  },
  data(){
    return{
        rules: 
        {
          adjustment: [{
                required: true,
                message: '请选择是否接受调剂',
                trigger: 'blur',
                }],
          department1: [{
                required: true,
                message: '请至少选择一个部门',
                trigger: 'blur',
                }],
          reason: [{
                required: true,
                message: '请填写原因',
                trigger: 'blur',
                }], 
        },
        options:[
                  {
                     value:'办公室',
                     label:'办公室'
                  },
                  {
                     value:'人力资源部',
                     label:'人力资源部'
                  },
                  { 
                     value:'品牌公关部',
                     label:'品牌公关部'
                  },
                  {
                    value:'对外拓展部',
                    label:'对外拓展部'
                  },
                  {
                    value:'创业指导部',
                    label:'创业指导部'
                  },
                  {
                    value:'职业发展部',
                    label:'职业发展部'
                  },
                  {
                    value:'技术推广部',
                    label:'技术推广部',
                    children:[
                      {
                        value:'技术组',
                        label:'技术组'
                      },
                      {
                        value:'美工组',
                        label:'美工组'
                      }
                    ]
                  }
        ]
  }
 },
 computed:{
    form:{
        get(){
            return this.PapicStore.$state.papic
        }
    } 
 },
 methods:{
    submit(){
         console.log(this.form)
         this.PapicStore.updatepapic(this.form)
         this.$confirm("请确认消息是否填写完整！","",
         {
          confirmButtonText:"确认",
          cancelButtonText:"取消",
          type:'warning'
         })
         .then(()=>{
          this.$message({
            message:'成功',
            type:'success'
          })
          this.PapicStore.submit()
         })
         .catch(()=>{
          this.$message({
            message:'已取消提交',
            type:'info',
            duration:2000
          })
         })
    },
    pre(){
      this.PapicStore.updatepapic(this.form)
      this.$router.push('/person')
    }
 },

//  mounted() {
//   console.log(this.PapicStore.papic)
//  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.back{
  top: 0;
  left: 0;
  position: fixed;
  /* overflow: scroll; */
  background-color: black;
  background-image: url(../assets/p4/p4-2.png),url(../assets/p4/p4-1.jpeg);
  width: 100%;
  height: 100%;
  background-size: 100% 100%,100% auto;
  background-repeat: repeat,no-repeat;
}
</style>
