<template>
  <div class="back">
    <el-container>
        <el-header style="display:flex;flex-direction:column;justify-content: center;" >
            <el-row justify="center" :gutter="20" align="middle">
                <el-col :span="3" @click="pre">
                    <el-image :src="require('@/assets/p3/p3-tanleft.png')" :fit="cover" />
                </el-col>
                    <el-col :span="6" >
                    继续？
                    </el-col>
                    <el-col :span="3" @click="next" >
                        <el-image :src="require('@/assets/p3/p3-tanright.png')" :fit="cover"/>
                    </el-col>
            </el-row>
        </el-header>
        <el-container>
            <el-aside width="40px" >
                <el-row align="bottom" style="height:50%">
                    <el-image :src="require('@/assets/p3/p3-tan.png')" :fit="cover" />
                </el-row>
                <el-row type="flex" align="top" style="height:50%" >
                    <el-col>
                        个人速写
                    </el-col>
                </el-row>
            </el-aside>
            <el-main>
                <el-row type="flex" justify="center" >
                        <el-col :xs="21" :sm="20" :md="12" :lg="12">
                            <el-form :model="form"  :rules="rules" ref="form" >
                                <el-form-item  prop="personality" label="性格特点概括(30字以内)" >
                                    <el-input 
                                    v-model="form.personality" 
                                    type="textarea" 
                                    :autosize="{minRows:4,maxRows:8}"
                                    maxlength="30"
                                    show-word-limit></el-input>
                                </el-form-item>
                                <el-form-item prop="skill" label="你的特长是？(多选)" >
                                    <el-checkbox-group v-model="form.skill">
                                        <el-row :gutter="20" type="flex" justify="start">
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="唱歌">唱歌</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="舞蹈">舞蹈</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="书法">书法</el-checkbox>
                                            </el-col>
                                        </el-row>
                                        <el-row :gutter="20" type="flex" justify="start">
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="摄影">摄影</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="主持">主持</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="乐器">乐器</el-checkbox>
                                            </el-col>
                                        </el-row>
                                        <el-row :gutter="20" type="flex" justify="start">
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="体育">体育</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="推文制作">推文制作</el-checkbox>
                                            </el-col>
                                        </el-row>
                                        <el-row :gutter="20" type="flex" justify="start">
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="文案">文案</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="网页制作">网页制作</el-checkbox>
                                            </el-col>
                                        </el-row>
                                        <el-row :gutter="20" type="flex" justify="start">
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="绘画">绘画</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="图像处理">图像处理</el-checkbox>
                                            </el-col>
                                        </el-row>
                                        <el-row :gutter="20" type="flex" justify="start">
                                            <el-col :span="6" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="活动策划">活动策划</el-checkbox>
                                            </el-col>
                                            <el-col :span="6" :push="3" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="音视频制作">音视频制作</el-checkbox>
                                            </el-col>
                                            <el-col :span="8" :push="7" style="display:flex;justify-content:flex-start;flex-direction">
                                                <el-checkbox label="其他" @click="addskills">{{form.others}}</el-checkbox>
                                            </el-col>
                                        </el-row>
                                    </el-checkbox-group>
                                </el-form-item>
                                <el-form-item  prop="abilities" label="请从以下能力选择两个优势点" >
                                    <el-checkbox-group v-model="form.abilities" :max="2" >
                                        <el-row :gutter="8">
                                            <el-col :span="8">
                                                <el-checkbox-button label="凝聚力"  style="display:flex;justify-content:center;flex-direction:column;">
                                                凝聚力
                                                </el-checkbox-button>
                                            </el-col>
                                            <el-col :span="8">
                                                <el-checkbox-button label="亲和力"  style="display:flex;justify-content:center;flex-direction:column;">
                                                亲和力
                                                </el-checkbox-button>
                                            </el-col>
                                            <el-col :span="8">
                                                <el-checkbox-button label="领导力"  style="display:flex;justify-content:center;flex-direction:column;">
                                                领导力
                                                </el-checkbox-button>
                                            </el-col>
                                        </el-row>
                                        <el-row :gutter="8">
                                            <el-col :span="8">
                                                <el-checkbox-button label="创新力"  style="display:flex;justify-content:center;flex-direction:column;">
                                                创新力
                                                </el-checkbox-button>
                                            </el-col>
                                            <el-col :span="8">
                                                <el-checkbox-button label="沟通力"  style="display:flex;justify-content:center;flex-direction:column;">
                                                沟通力
                                                </el-checkbox-button>
                                            </el-col>
                                            <el-col :span="8">
                                                <el-checkbox-button label="适应力"  style="display:flex;justify-content:center;flex-direction:column;">
                                                适应力
                                                </el-checkbox-button>
                                            </el-col>
                                        </el-row>
                                    </el-checkbox-group>
                                </el-form-item>
                            </el-form>
                        </el-col>
                    </el-row>
            </el-main>
        </el-container>
    </el-container>
    
  </div>
</template>

<script>
import { Form } from "../store/index";
export default {
  name: 'PersonPage',
  setup() {
    const PersonStore=Form()
    return {PersonStore}
  },
  data() {
    return {
        rules: {
            personality: [{
                required: true,
                message: '请输入你的答案',
                trigger: 'blur',
                }],
            skill:[{
                type:'array',
                required: true,
                message: '请至少选择一个特长',
                trigger: 'blur',
            }],
            abilities:[{
                type:'array',
                required: true,
                message: '请输入你的答案',
                trigger: 'blur',
            }]
        }
    }
  },
  computed:{
    form:{
        get(){
            return this.PersonStore.$state.person
        }
    }
  },
  methods: {
    next(){
        this.PersonStore.updateperson(this.form)
        this.$router.push('/papic')
    },
    pre(){
        this.PersonStore.updateperson(this.form)
        this.$router.push('/basic')   
    },
    addskills(){
        this.form.oskill_checked=!this.form.oskill_checked
        if(this.form.oskill_checked){
            this.$prompt("请输入你其他的特长(8字以内)","其他",{
                confirmButtonText:"确定",
                showCancelButton:false,
                closeOnClickModal: false,
                closeOnPressEscape: false,
                closeOnHashChange: false,
                inputPlaceholder: "请输入你的其它特长哦",
                center:true,
                roundButton: true,
                inputPattern:/^.+$/,
                inputErrorMessage:"不能为空哦",
            })
            .then(({value})=>{
                this.form.others=value
            })
            .catch(()=>{
                this.$message({
                    type:"info",
                    message:"取消输入"
                })
            })
        }
        else{
            this.form.others="其他"
        }
    }
  },
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.back{
  top: 0;
  left: 0;
  position: fixed;
  overflow: scroll;
  background-color: black;
  background-image: url(../assets/p3/p3-2.png),url(../assets/p3/p3-1.jpeg);
  width: 100%;
  height: 100%;
  background-size: 100% 100%,100% auto;
  background-repeat: repeat,no-repeat;
}
.el-form-item__label{
    justify-content: flex-start;
}
.el-form-item{
    flex-direction: column;
}
</style>
